import React from 'react';
import { Form, Radio, Checkbox, Button, Notify, Pop, Icon } from 'zent';
const { Field, FormInputField, FormSelectField, FormRadioGroupField, FormCheckboxField,
    FormCheckboxGroupField, FormColorPickerField,
    FormDateRangePickerField, FormNumberInputField, FormSwitchField,
    FormSection, FieldArray, createForm } = Form;
import './tab-bar-config.pcss'
class Basic extends React.Component {
    state = {
        checkedList: []
    }

    constructor(props) {
        super(props)
    }
    render() {
        const { fields } = this.props;

        return (
            <div>
                <div><Button type="primary" outline onClick={this.addTabBarBtn}>TabBarButton</Button></div>
                {fields.map((name, index, key, item, fieldsValue) => {
                    return (
                        <div className="tab-bar-container" key={`tabBar${key}`}>
                            <span className="tab-bar-title"> TabBarButton {index + 1} </span>
                            <Pop centerArrow trigger="hover" content="">
                                <Icon
                                    className="del-btn"
                                    type="close-circle"
                                    onClick={() => fields.remove(index)}
                                />
                            </Pop>
                            <FormSection name={name}>
                                <FormInputField
                                    name="text"
                                    type="text"
                                    label="Tab:"
                                    required
                                    spellCheck={false}
                                    validations={{ required: true }}
                                    validationErrors={{ required: 'Tab' }}
                                />
                                <FormInputField
                                    name="iconUrl"
                                    type="text"
                                    label=":"
                                    spellCheck={false}
                                    validationErrors={{ required: '' }}
                                    value={window.navigationBarTitleText}
                                />
                                <FormInputField
                                    name="selectedIconUrl"
                                    type="text"
                                    label=":"
                                    spellCheck={false}
                                    validationErrors={{ required: '' }}
                                    value={window.navigationBarTitleText}
                                />
                                <FormSelectField
                                    name="pageStructure"
                                    label=":"
                                    data={[
                                        { value: 'dynamic', text: '' },
                                        { value: 'static', text: '' }
                                    ]}
                                    required
                                    validations={{ required: true }}
                                    validationErrors={{ required: '' }}
                                    value={window.navigationBarTextStyle}
                                />
                                <FormSelectField
                                    name="pageStructure"
                                    label=":"
                                    data={[
                                        { value: 1, text: '' },
                                        { value: 2, text: '' }
                                    ]}
                                    required
                                    validations={{ required: true }}
                                    validationErrors={{ required: '' }}
                                    value={window.navigationBarTextStyle}
                                />
                               <FormSelectField
                                    name="pageStructure"
                                    label=":"
                                    data={[
                                        { value: 1, text: '' },
                                        { value: 2, text: '' }
                                    ]}
                                    required
                                    validations={{ required: true }}
                                    validationErrors={{ required: '' }}
                                    value={window.navigationBarTextStyle}
                                />
                            </FormSection>
                        </div>
                    );
                })}
            </div>
        );
    }

    addTabBarBtn = () => {
        this.props.fields.push({
            "pageId": 1,
            "pageName": "",
            "pageStructure": "static",
            "contentId": "",
            "iconUrl": "",
            "selectedIconUrl": "",
            "text": " " + this.props.fields.length
        });
    };


}

export default Basic